* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  height: 100vh;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

.login-page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.login-form {
  text-align: center;
  background: #fff;
  border-radius: 10px;
  padding: 40px;
  box-shadow: 0 10px 40px #0003;
}

.login-form h1 {
  color: #667eea;
  margin-bottom: 30px;
}

.username-input {
  border: 2px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
  padding: 12px;
  font-size: 16px;
}

.login-btn {
  color: #fff;
  cursor: pointer;
  background: #667eea;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  transition: background .3s;
}

.login-btn:hover {
  background: #5568d3;
}

.lobby-page {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  flex-direction: column;
  height: 100vh;
  display: flex;
}

.lobby-header {
  color: #fff;
  background: #0000004d;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  display: flex;
}

.lobby-header h1 {
  margin: 0;
  font-size: 28px;
}

.lobby-content {
  flex: 1;
  grid-template-columns: 1fr 2fr;
  gap: 30px;
  padding: 30px;
  display: grid;
  overflow: hidden;
}

.create-room-section {
  background: #fff;
  border-radius: 15px;
  height: fit-content;
  padding: 30px;
  box-shadow: 0 10px 30px #0003;
}

.create-room-section h2 {
  color: #667eea;
  margin-top: 0;
  margin-bottom: 20px;
}

.create-room-section input[type="text"], .create-room-section input[type="password"], .create-room-section input[type="number"] {
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 15px;
  padding: 12px 15px;
  font-size: 14px;
  transition: border-color .3s;
}

.create-room-section input:focus {
  border-color: #667eea;
  outline: none;
}

.room-settings {
  flex-direction: column;
  gap: 15px;
  margin: 20px 0;
  display: flex;
}

.room-settings label {
  color: #333;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  display: flex;
}

.room-settings input[type="checkbox"] {
  cursor: pointer;
  width: 18px;
  height: 18px;
}

.room-settings input[type="number"] {
  width: 80px;
  margin-bottom: 0;
}

#btn-create-room {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 15px;
  font-size: 16px;
  font-weight: bold;
  transition: transform .2s, box-shadow .2s;
}

#btn-create-room:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px #667eea66;
}

#btn-create-room:active {
  transform: translateY(0);
}

.room-list-section {
  background: #fff;
  border-radius: 15px;
  flex-direction: column;
  max-height: calc(100vh - 160px);
  padding: 30px;
  display: flex;
  box-shadow: 0 10px 30px #0003;
}

.room-list-section h2 {
  color: #667eea;
  margin-top: 0;
  margin-bottom: 20px;
}

.room-list-controls {
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

#search-rooms-input {
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  flex: 1;
  padding: 10px 15px;
  font-size: 14px;
}

#search-rooms-input:focus {
  border-color: #667eea;
  outline: none;
}

#btn-refresh-rooms {
  color: #fff;
  cursor: pointer;
  background: #667eea;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-weight: 600;
  transition: background .3s;
}

#btn-refresh-rooms:hover {
  background: #5568d3;
}

.room-list {
  flex: 1;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  padding-right: 10px;
  display: grid;
  overflow-y: auto;
}

.room-card {
  color: #fff;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 12px;
  flex-direction: column;
  gap: 15px;
  min-height: 180px;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 4px 15px #0000001a;
}

.room-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px #0003;
}

.room-card-header {
  justify-content: space-between;
  align-items: flex-start;
  gap: 10px;
  display: flex;
}

.room-name {
  word-break: break-word;
  flex: 1;
  margin: 0;
  font-size: 18px;
  font-weight: bold;
}

.room-status {
  white-space: nowrap;
  background: #fff3;
  border-radius: 12px;
  padding: 4px 10px;
  font-size: 14px;
}

.room-card-body {
  flex: 1;
}

.room-host, .room-age {
  opacity: .9;
  margin: 5px 0;
  font-size: 14px;
}

.btn-join-room {
  color: #667eea;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 12px;
  font-size: 15px;
  font-weight: bold;
  transition: all .2s;
}

.btn-join-room:hover:not(:disabled) {
  background: #f0f0f0;
  transform: scale(1.02);
}

.btn-join-room:disabled {
  opacity: .5;
  cursor: not-allowed;
  color: #999;
  background: #e0e0e0;
}

.no-rooms {
  text-align: center;
  color: #999;
  grid-column: 1 / -1;
  padding: 40px;
  font-size: 16px;
}

.room-list::-webkit-scrollbar {
  width: 8px;
}

.room-list::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.room-list::-webkit-scrollbar-thumb {
  background: #667eea;
  border-radius: 10px;
}

.room-list::-webkit-scrollbar-thumb:hover {
  background: #5568d3;
}

.room-page {
  flex-direction: column;
  height: 100vh;
  display: flex;
}

.room-header {
  background: #fffffff2;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  display: flex;
  box-shadow: 0 2px 10px #0000001a;
}

.room-title-section {
  align-items: center;
  gap: 15px;
  display: flex;
}

.room-header h1 {
  color: #667eea;
  margin: 0;
  font-size: 24px;
}

.btn-leave-room {
  color: #fff;
  cursor: pointer;
  background: #dc3545;
  border: none;
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 600;
  transition: background .3s;
}

.btn-leave-room:hover {
  background: #c82333;
}

.user-count {
  color: #666;
  font-size: 14px;
}

.room-content {
  flex: 1;
  gap: 20px;
  padding: 20px;
  display: flex;
  overflow: hidden;
}

.left-panel, .center-panel, .right-panel {
  background: #fffffff2;
  border-radius: 10px;
  flex-direction: column;
  padding: 20px;
  display: flex;
  box-shadow: 0 5px 20px #0000001a;
}

.left-panel {
  flex: 0 0 300px;
}

.center-panel {
  flex: 1;
}

.right-panel {
  flex: 0 0 350px;
}

h2 {
  color: #667eea;
  margin-bottom: 15px;
  font-size: 18px;
}

.audio-section {
  margin-bottom: 20px;
}

.audio-upload {
  margin-bottom: 15px;
}

.btn-upload {
  color: #fff;
  cursor: pointer;
  background: #667eea;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  transition: background .3s;
}

.btn-upload:hover {
  background: #5568d3;
}

.audio-player audio {
  width: 100%;
  margin-bottom: 15px;
}

.playlist {
  max-height: 200px;
  overflow-y: auto;
}

.playlist h3 {
  color: #666;
  margin-bottom: 10px;
  font-size: 14px;
}

.playlist-items {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.playlist-item {
  cursor: pointer;
  background: #f5f5f5;
  border-radius: 5px;
  padding: 8px;
  font-size: 13px;
  transition: background .2s;
}

.playlist-item:hover {
  background: #e0e0e0;
}

.voice-section h2 {
  margin-bottom: 10px;
}

.btn-talk {
  color: #fff;
  cursor: pointer;
  background: #28a745;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 15px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
}

.btn-talk:active, .btn-talk.talking {
  background: #dc3545;
  transform: scale(.98);
}

.voice-status {
  color: #666;
  background: #f5f5f5;
  border-radius: 5px;
  min-height: 80px;
  margin-top: 10px;
  padding: 10px;
  font-size: 13px;
}

.voice-indicator {
  background: #e0e0e0;
  border-radius: 3px;
  margin: 2px 0;
  padding: 5px;
}

.voice-indicator.active {
  color: #fff;
  background: #28a745;
}

.whiteboard-section {
  flex-direction: column;
  height: 100%;
  display: flex;
}

.whiteboard-controls {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

.colors {
  gap: 10px;
  display: flex;
}

.color {
  cursor: pointer;
  border: 3px solid #0000;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  transition: transform .2s;
}

.color:hover {
  transform: scale(1.1);
}

.color.selected {
  border-color: #667eea;
}

.color.black {
  background: #000;
}

.color.red {
  background: red;
}

.color.green {
  background: green;
}

.color.blue {
  background: #00f;
}

.color.yellow {
  background: #ff0;
}

.color.white {
  background: #fff;
  border: 2px solid #ccc;
}

.btn-clear-canvas {
  color: #fff;
  cursor: pointer;
  background: #dc3545;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 14px;
  transition: background .3s;
}

.btn-clear-canvas:hover {
  background: #c82333;
}

.whiteboard {
  cursor: crosshair;
  background: #fff;
  border: 2px solid #ddd;
  border-radius: 5px;
  flex: 1;
}

.chat-section {
  flex-direction: column;
  height: 100%;
  display: flex;
}

.messages {
  background: #f9f9f9;
  border-radius: 5px;
  flex: 1;
  margin-bottom: 15px;
  padding: 10px;
  overflow-y: auto;
}

.message {
  word-wrap: break-word;
  background: #fff;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 8px;
}

.message.system {
  color: #1976d2;
  text-align: center;
  background: #e3f2fd;
  font-size: 13px;
  font-style: italic;
}

.message.own {
  color: #fff;
  background: #667eea;
  margin-left: 20px;
}

.message .username {
  color: #667eea;
  margin-bottom: 3px;
  font-weight: bold;
}

.message.own .username {
  color: #fff;
}

.message .text {
  font-size: 14px;
}

.typing-indicator {
  color: #999;
  padding: 5px;
  font-size: 12px;
  font-style: italic;
}

.input-area {
  gap: 10px;
  display: flex;
}

.message-input {
  border: 2px solid #ddd;
  border-radius: 5px;
  flex: 1;
  padding: 10px;
  font-size: 14px;
}

.send-btn {
  color: #fff;
  cursor: pointer;
  background: #667eea;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-weight: bold;
  transition: background .3s;
}

.send-btn:hover {
  background: #5568d3;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
